<template>
	<div class="swiper-container" :class="swipeid">
		<div class="swiper-wrapper">
			<slot name="swiper-con"></slot>
			<span>{{ num }}</span>
		</div>
		<!-- 分页器 -->
		<div :class='{"swiper-pagination":pagination}' :style="{'text-align':paginationDirection}"></div>
	</div>
</template>
<script type="text/javascript">
	import '../assets/libs/swiper/js/swiper.min.js'
	export default{

		props: {
			text:{

			},
			num: {
			type:String,
			default:""


			},
			swipeid: {
				type: String,
				default:''
			},
			effect: {
				type: String,
				default: 'slide'
			},
			loop: {
				type: Boolean,
				default: true
			},
			direction: {
				type: String,
				default: 'horizontal'
			},
			autoplay: {
				type: Number,
				default: 500

			},
			paginationType: {
				type:String,
				default: 'bullets'
			},
			pagination: {
				type: Boolean,
				default: true
			},
			paginationDirection: {
				type: String,
				default: 'center'
			},
			slidesPerView: {
				type:[Number,String],
				default: "1"
			},
			loopedSlides:{
				type:Number,
				default:1
			},
			autoplayDisableOnInteraction:{
				type:Boolean,
				default:false
			},
				
		},
		mounted: function () {
			// body...
			var _this = this;
			new Swiper('.'+_this.swipeid,{
				//循环
				loop: _this.lop,
				//分页器
				pagination: '.swiper-pagination',
				//分页类型
				paginationType: _this.paginationType,
				//自动播放
				autoplay: _this.autoplay,
				//方向
				direction: _this.direction,
				//特效
				effect: _this.effect,
				//用户操作swiper之后，是否禁止autoplay
				autoplayDisableOnInteraction: _this.autoplayDisableOnInteraction,
				//slidesPerView:3,
				loopedSlides:_this.loopedSlides,
				slidesPerView:_this.slidesPerView
			})
		}
	}
</script>
<style type="text/css">
	@import "../assets/libs/swiper/css/swiper.min.css";
	.swiper-container .homeBanner{width: 100%;height: 3.68rem;margin-top: 2.5rem}
	.swiper-container .film-img-wrap{width: 33%;text-align: center;float: left;}
	.swiper-container .filmImg{width: 3rem;height: 4.18rem}
	.swiper-container .start{text-align: left;line-height: .48rem;margin-left: .45rem;height: .48rem;}
	.swiper-container .startImg{width:.48rem;float: left; }
	.swiper-container .num{font-size: .38rem;float: left;line-height: .48rem;margin-left: .1rem}
	.swiper-container .swiper-slide{width:4.14rem;}

</style>


