<template>
	<div class="filmTitle">
		<h2>{{ title }}</h2>
		<a href="javascript:;">{{ more }}</a>
	</div>
</template>
<script type="text/javascript">
	export default{
		props:["title","more"]
	}
</script>
<style type="text/css">
	.filmTitle{height: 1.95rem;line-height: 1.95rem;padding-left: 0.5rem;padding-right: 0.5rem}
	.filmTitle h2{float: left;font-size: .5rem;color: #494949}
	.filmTitle a{float: right;font-size: .5rem;color: #42bd56;text-decoration: none;}
</style>



