<template>
	<div>
		<div class="homeNews"  @click="fn()">
			<div class="clearfix ">
				<div class="left">
					<h3>{{ title}}</h3>
					<p v-html="news" class="content"></p>
					<span class="xian"></span>	
				</div>
				<div class="right"> 
					<img :src="img" />				
				</div>
			</div>
			<div class="text"><span class="author">{{ author }}</span><span class="origin">{{ origin }}</span></div>
		</div>
	</div>
</template>
<script type="text/javascript">
import request from 'superagent'
import jsonp from 'superagent-jsonp' 
	export default{

		props:["title","news","img","author","origin"],
		methods: {
		
			fn:function(i){
				
				this.$router.push("/detailed")
			},
			fnArr2:function(i){
				this.$router.push("/detailed")
			}
		},
		
	}
</script>
<style type="text/css">
	.clearfix:after{content: ".";display: block;visibility: hidden;height: 0;clear: both;}
	.homeNews {padding: .5rem; border-bottom: .01rem solid #9b9b9b;}
	.homeNews .left{width: 7.6rem;float: left;}
	.homeNews h3{font-size: .55rem;color: black;}

	/*请求本地数据的部分*/
	/*.homeNews p{font-size: .36rem;color: #9b9b9b;padding: .20rem 0;}*/

	/*请求网络的部分*/
	.homeNews .content{font-size: .36rem;color: #9b9b9b;padding: .20rem 0;overflow: hidden;height: 2rem}


	.homeNews .author{font-size: .34rem;color: #9b9b9b;float: left;}
	.homeNews .xian{display: block;width: .45rem;height: .01rem;border-bottom:.01rem solid #9b9b9b;}
	.homeNews img{width: 2.7rem}
	.homeNews .right{float: right;margin-right: 1rem}
	.homeNews .origin{font-size: .34rem;color: #9b9b9b;float: right;margin-right: 1rem}
	.homeNews .text{height: 1rem;width: 100%;line-height: 1rem}
</style>



