<template>
	<div>
		<Top bol=false></Top>
		<Banner swipeid='swipeid01' >
			<div class="swiper-slide" slot='swiper-con'>
				<img src="../../assets/images/banner/01.jpg" class="homeBanner" />
			</div>
			<div class="swiper-slide" slot='swiper-con'>
				<img src="../../assets/images/banner/02.jpg" class="homeBanner"/>
			</div>
			<div class="swiper-slide" slot='swiper-con'>
				<img src="../../assets/images/banner/03.jpg" class="homeBanner"/>
			</div>
		</Banner>
		<HomeTitle title="热点" ></HomeTitle>
		<!-- <div v-for = "item in hotNews">
			<router-link to="destail" >
				<HomeNews :title="item.title" :news="item.news" :img="item.img" :author="item.author" :origin="item.origin"></HomeNews>
			</router-link>
		</div> -->



		<div v-for = "item in hotNews">
			<router-link :to="{name:'Destail',params:{id:item.id}}" >
				<HomeNews :title="item.title" :news="item.content" :img="item.image" :author="item.category_name" :origin="item.address"></HomeNews>
			</router-link>
		</div>
		
		
		<HomeTitle title="根据推荐" :bol="true"></HomeTitle>
		<div v-for = "item in time ">
			<HomeNews :title="item.caption" :news="item.content" :img="item.img" :author="item.author" :origin="item.origin"></HomeNews>
		</div>
			<infinite-loading :on-infinite="onInfinite" ref="infiniteLoading">
			    <span slot="no-more">
			    已结没有更多数据了
			  	</span>
		    </infinite-loading>

		

		<router-view></router-view>
	</div>
</template>
<script type="text/javascript">
	 import Banner from '../../components/banner'
	import Top from '../../components/top'
	import HomeTitle from './homeTitle'
	import HomeNews from './homeNews'
	import InfiniteLoading from 'vue-infinite-loading';


	import request from 'superagent'
	import jsonp from 'superagent-jsonp' 
	export default {
		components: {
			
			Top,
			Banner,
			HomeTitle,
			HomeNews,
			InfiniteLoading


		},


			// 请求本地数据
		data:function(){
			return{
				hotNews:[],
				groom:[],
				
				time:[],
				start:0
			}
		},

		// 请求本地数据
		// mounted: function(){
		// 	var _this = this;
		// 	this.axios.get('../../../static/data/txt.json').then(function(res){
		// 		 _this.hotNews = res.data
		// 		    console.log(res.data)

		// 	});
		// 	this.axios.get('../../../static/data/doubanTime.json').then(function(res){
		// 		 _this.groom = res.data
		// 		    // console.log(res)

		// 	});

	 //     }  



	 // 请求网络数据
	 // data:function(){
		// 	return{
		// 		hotNews:[],
		// 		groom:[]
		// 	}
		// },

	 mounted: function(){
	 	var	$this = this
	 	// 网络请求
			request.get("https://api.douban.com/v2/event/list?loc=108288&start=0&count=10")
		        .use(jsonp)
		        .end((err,res) => {
		        if (!err) {

		        	$this.hotNews = res.body.events

		        	console.log(res.body.events)
		     	 		}
		  			}), 







				//本地请求
			this.axios.get('static/data/doubanTime.json').then(function(res){
					 $this.groom = res.data
					    // console.log(res)
					    //在请求时就加入两条数据
					    $this.time=$this.groom.slice($this.start,$this.start+2)
		        		$this.start+=2
				})		


			},
			methods:{
				onInfinite:function(){
					var _this = this
					setTimeout(function(){
					var	temp=_this.groom.slice(_this.start,_this.start+2)
						_this.time=_this.time.concat(temp)
						 _this.$refs.infiniteLoading.$emit('$InfiniteLoading:loaded');
						 if(_this.time.length=='10'){
						 	 _this.$refs.infiniteLoading.$emit('$InfiniteLoading:complete');
						 }

						 _this.start+=2;
					},2000);
				}


			}




		
	}
</script>
<style type="text/css">
	
</style>


