<template>
<div>
	<div class="comments clearfix" v-for = "(item,index) in commentsArr">
		<span class="del" @click = "del(index)">x</span>
		<!-- <slot name="pic"></slot> -->
		<img :src="item.pic" class="pic">
		<div class="texts clearfix">
			<div class="nam">
				<span class="names">{{ item.names }}</span>
				<span class="notice"><i>{{ item.num }}</i>人关注</span>	
			</div>
			<span class="follow" @click = "follow(index)" v-show = "item.bol">关注</span>
			<span class="followed" v-show = "!item.bol" @click = "followed(index)">已关注</span>
		</div>
		<p>{{ item.content }}</p>
		<!-- <slot name="condent"></slot> -->
		<img :src="item.img" class="conImg"  v-show ="item.isshow" >
		<span class="praise" >
			<img src="../../assets/images/ic_liked.png"  class="zan"  @click  = "zan(index)">
			<span class="nums" :class="{num1:item.iscolor}" >{{ item.num1 }}</span>
		</span>
		<span class="txt">
			<!-- <slot name="discuss"></slot> -->
			<img :src="item.discuss" class="discuss">
			<span class="num2" >{{ item.num2 }}</span>
		</span>
	</div>
</div>
</template>
<script type="text/javascript">
	export default {
		// props:["num","names","content","num1","num2"],
		props:{

			commentsArr:{
				type:Array,
				require:true
			},

		},
		methods:{
			
			follow:function (i) {
				if(this.commentsArr[i].bol ==this.commentsArr[i].bol){
						this.commentsArr[i].bol = false;
						this.commentsArr[i].num++
				}
			},
			followed:function (i) {
				if(this.commentsArr[i].bol ==false){
						this.commentsArr[i].bol = true;
						this.commentsArr[i].num--
				}
			},
			zan:function (index) {
				// body...

				if(this.commentsArr[index].iscolor ==false){
					this.commentsArr[index].num1++;
					this.commentsArr[index].iscolor = true;
				}else{
					this.commentsArr[index].iscolor =false;
					this.commentsArr[index].num1--;
				}
				
			},
			del:function (index) {
				// body...
				this.commentsArr.splice(index,1)
			},

		}




	}
</script>
<style type="text/css">
	.clearfix:after{content: ".";display: block;visibility: hidden;height: 0;clear: both;}
	.comments .texts{padding-bottom: .4rem}
	.comments .del{position: absolute;font-size: .5rem;top:.2rem;right: .5rem}
	.comments .nam{float: left;line-height: .5rem}
	.comments{width: 8.60rem;margin-left: .35rem;border:.01rem solid #999;position: relative;padding: .80rem .80rem .60rem 2rem;margin-top: .40rem}
	.comments .pic{width: 1.52rem;position: absolute;top:0.5rem;left: .45rem}
	.comments .notice{font-size: .34rem;color: #9b9b9b;}
	.comments .names{font-size: .5rem;color: #494949;display: block;}
	.comments .follow{display: block;height: .84rem;width: 2.25rem;line-height: .84rem;color: #fff;background: #42bd56;font-size: .36rem;text-align: center;float: right;border-radius: .1rem;margin-top: .2rem}
	.comments .followed{display: block;height: .84rem;width: 2.25rem;line-height: .84rem;color: #fff;background: #999;font-size: .36rem;text-align: center;float: right;border-radius: .1rem;margin-top: .2rem}
	.comments p{font-size: .5rem;color: #494949; }
	.comments .conImg{width: 8.56rem;height: 4.22rem;padding-top: .40rem  }
	.comments .praise{height: .6rem;line-height: .6rem;float: left;padding-top: .2rem}
	.comments .zan{width: 0.6rem;float: left;}
	.comments .nums{font-size: .34rem;color: black;display: block;float: left;}
	.comments .praise .num1{color: #ccc}
	.comments .discuss{width: 0.6rem;height: .6rem;float: left;}
	.comments .txt{margin-left:.5rem;float: left;height: .6rem;line-height: .6rem;padding-top: .2rem}
	.comments .num2{font-size: .34rem;color: #ccc;display: block;float: left;}
</style>


