<template>
	<div>
		<Top txt="小组">
			<img src="../../assets/images/ic_group_search.png" slot="topSearch" class="left"/>
			<img src="../../assets/images/ic_create_group_chat_blue.png"  slot="topIcon" class="right"/>
		</Top>
		<GroupJion txt1="418975个小组等你来玩" txt2="立即选择加入吧!" txt3="根据你的兴趣推荐"
		 txt4="精选推荐" txt5="影视">
			<img src="../../assets/images/ic_group_banner.png" height="513" width="1080" slot="pic" class="pic">
			<img src="../../assets/images/ic_rec_group_banner_6.png" height="225" width="1035" slot="img" class="img">
		</GroupJion>
		<GroupTitle txt="影视" v-if="trade=='show'"></GroupTitle>


		<FilmBrief v-for = "item in filmBrief"  isshow = "filmBrief" v-if="trade=='show'">
			<div class="filmBrief clearfix" slot="filmBrief">
				<img :src="item.img" class="touXiang">
				<div class="text">
					<h2>{{ item.title }}</h2>
					<div class="brief">{{ item.brief }}</div>
				</div>
				<span>{{ item.num }}<i>人</i></span>
				<div class="select"></div>
				
			</div>
		</FilmBrief>
		<GroupTitle txt="读书" v-if="trade=='show'"></GroupTitle>
		<FilmBrief v-for = "item in filmread"  isshow = "filmread" v-if="trade=='show'">
			<div class="filmBrief clearfix" slot="filmBrief">
				<img :src="item.img" class="touXiang">
				<div class="text">
					<h2>{{ item.title }}</h2>
					<div class="brief">{{ item.brief }}</div>
				</div>
				<span>{{ item.num }}<i>人</i></span>
				<div class="select"></div>
				
			</div>
		</FilmBrief>

		<GroupTitle txt="音乐" v-if="trade=='hide'"></GroupTitle>
		<FilmBrief v-for = "item in groupMusic"  isshow = "groupMusic" v-if="trade=='hide'">
			<div class="filmBrief clearfix" slot="filmBrief">
				<img :src="item.img" class="touXiang">
				<div class="text">
					<h2>{{ item.title }}</h2>
					<div class="brief">{{ item.brief }}</div>
				</div>
				<span>{{ item.num }}<i>人</i></span>
				<div class="select"></div>
				
			</div>
		</FilmBrief>
		<div class="briefUpdate">
			<div class="huan" @click = "exchange()">
				<img src="../../assets/images/ic_reshare_flag.png">
				<span>换一批</span>
			</div>
			<div class="choose">选好了，去玩小组</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import Top from '../../components/top'
	import GroupJion from './groupJion'
	import FilmBrief from './filmBrief'
	import GroupTitle from './groupTitle'

	export default{
		components:{
			Top,
			GroupJion,
			FilmBrief,
			GroupTitle
		},

			// 请求本地数据
		data:function(){
			return{
				filmBrief:[],
				filmread:[],
				groupMusic:[]
			}
		},

		// 请求本地数据
		mounted: function(){
			var _this = this;
			this.axios.get('static/data/groupfilm.json').then(function(res){
				 _this.filmBrief = res.data
				    // console.log(res.data)

			});
			this.axios.get('static/data/groupread.json').then(function(res){
				 _this.filmread = res.data
				    // console.log(res.data)

			});
			this.axios.get('static/data/groupMusic.json').then(function(res){
				 _this.groupMusic = res.data
				    // console.log(res.data)

			});
	     },

	     props:{
	     	trade:{
	     		type:String,
	     		default:'show'
	     	}
	     },


	     // data:function () {
	     // 	// body...
	     // 	return{
	     // 		trade:'show'
	     // 	}
	     // },

	     methods:{
	     	exchange:function () {
	     		// body...
	     		if(this.trade =='show'){
	     			this.trade = 'hide'
	     		}
	     		else if(this.trade =='hide'){
	     			this.trade = 'show'
	     		}

	     		
	     	}
	     }  




	}

</script>
<style type="text/css">
	.briefUpdate{width: 100%;height: 1.5rem;position: fixed;bottom: 1.5rem;left: 0;z-index: 2;background: #fff}
	.briefUpdate .huan{float: left;margin-left:.45rem;height: .8rem;margin-top: .4rem}
	.briefUpdate img{width: .6rem;height: .6rem;float: left;}
	.briefUpdate span{font-size: .38rem;color: #42bd56;float: left;margin-left: .15rem}
	.briefUpdate .choose{height: .8rem;width: 3.80rem;float: right;color: #fff;font-size:.38rem;background: #e2e2e2;border-radius: .05rem;line-height: .8rem;text-align: center;margin-right: .5rem;margin-top: .3rem }
</style>


