<template class="broadcast">
	<div class="broadcastCon">
		<Top txt="广播" >
			<img src="../../assets/images/ic_group_chat_setting.png" slot="topSearch" class="left"/>
			<img src="../../assets/images/ic_create_group_chat_blue.png"  slot="topIcon" class="right"/>

		</Top>
		<div class="follow">
			<img src="../../assets/images/ic_status_anonymous_header.png"  >
			<div class="text">
				<p class="title">发现有趣的人</p>
				<p class="con">关注他们发现更大的世界</p>
			</div>
		</div>
		<!-- <Comments names="胡桑" num=4785 content="爱指向处于虚弱中的他人" num1=12 num2=1>
			<img src="../../assets/images/touxiang_03.png"  slot="pic" class="pic">
			<img src="../../assets/images/content_07.png" height="422" width="856" slot="condent" class="conImg">
			<img src="../../assets/images/ic_group_comment_0.png"  slot="discuss" class="discuss">
		</Comments>
		<Comments names="靖远居士" num=4785 content="有时候对自己的性格无语：太过积极，太过果断。但凡喜欢的人和物，总是难逃我的魔掌。但凡不喜欢的，就再也看不见了。对于一个中年文艺男子来说，心里竟然没有白夜光，没有与前任的恩怨纠缠，真是不科学" num1=12 num2=1>
			<img src="../../assets/images/tou2_03.png"  slot="pic" class="pic">
			<img src="../../assets/images/ic_group_comment_0.png"  slot="discuss" class="discuss">
		</Comments> -->
		<Comments  :commentsArr = "broadcastArr" :isshow="0"></Comments>

	</div>
</template>
<script type="text/javascript">
	import Top from '../../components/top'
	import Comments from './comments'
	export default{
		components:{
			Top,
			Comments
		},

			// 请求本地数据
		data:function(){
			return{
				broadcastArr:[],
				
			}
		},

		// 请求本地数据
		mounted: function(){
			var _this = this
			this.axios.get('static/data/broadcast.json').then(function(res){
				 _this.broadcastArr = res.data
				     // console.log(res.data)

			})
			
	     }  

	}

</script>
<style type="text/css">
	.broadcastCon .follow{height: 3.50rem;margin-top: 2.5rem;}
	 .follow img{width: 2.70rem;height: 2.34rem;float: left;margin-left: 2.14rem;padding-top: .6rem}
	.broadcastCon .text {float: left;margin-left: .5rem;padding-top: 1.28rem}
	.broadcastCon .title{font-size: .5rem;color: #494949}
	.broadcastCon .con{font-size: .36rem;color: #9b9b9b}
</style>


