<template>
	<div class="homeTitle">
		<h2 :class="{green:bol==true}">{{ title }}</h2>
	</div>
</template>
<script type="text/javascript">
	export default{
		props:{
			title:{
				type:String,
				default:''
			},
			bol:{
				type:Boolean,
				default:false
			}
		},
		
			
		

	}

</script>
<style type="text/css">
	.homeTitle{margin-top: .48rem}
	.homeTitle h2{height: 0.6rem;line-height: 0.6rem;border-left: .12rem solid #ff8447;font-size:.48rem; color:#ff8447;text-align: left;padding-left:.5rem; }
	.homeTitle .green{border-left: .12rem solid #42bd56;color:#42bd56;}
</style>


