<template>
	<div class="groupJion">
		<div class="top">
			<slot name='pic'></slot>
			<p>{{ txt1 }}</p>
			<span class="jion">{{ txt2 }}</span>
			<span class="groom">{{ txt3 }}</span>
		</div>
		<div class="bot">
			<slot name="img"></slot>
			<div class="choice">{{ txt4 }}</div>
			<!-- <div class="fl-tv">{{ txt5 }}</div> -->
		</div>
	</div>
	


</template>
<script type="text/javascript">
	export default{
		props:['txt1',"txt2",'txt3','txt4',]
	} 
</script>
<style type="text/css">
	.groupJion{margin-top: 2.5rem}
	.groupJion .top{position: relative;text-align: center;margin-bottom: .70rem}
	.groupJion .top .pic{width: 12.42rem;height: 5.13rem;}
	.groupJion .top p{font-size: .5rem;color: #494949;position: absolute;top: 34%;left: 30%}
	.groupJion .top .jion{font-size: .38rem;color: #9b9b9b;position: absolute;top: 50%;left: 39%}
	.groupJion .top .groom{position: absolute;bottom: 0;left: .36rem;font-size: .38rem;color: #9b9b9b}
	.groupJion .bot{position: relative;width: 11.68rem;margin-left: .36rem}
	.groupJion .choice{position: absolute;font-size: .42rem;top: 1.5rem;left: .3rem;color: #fff}
	/*.groupJion .fl-tv{position: absolute;font-size: .42rem;color: #494949;bottom:-.3rem;left: 36%;background: #fff;padding: 0 1rem}*/
	.groupJion .bot .img{width: 11.68rem;height: 2.25rem;}
</style>


