<template>
	<div class="topWrap" >
	<div class="topInner" >
		<span>{{ txt }}</span>
		<slot name="topSearch"></slot>
		<slot name="topIcon"></slot>
	</div>
	
	<div class="searchWrap" v-show="bol" >
		<img src="../assets/images/ic_search_gray.png" class="search" />
		<input type="text" placeholder="影视 图书">
		<img src="../assets/images/big_03.png"  class="big"/>
		<img src="../assets/images/yun_03.png"  class="icon"/>
	</div>
	

	 </div>



</template>
<script type="text/javascript">
	export default {
		props:["txt","bol"],
		
	}
</script>
<style type="text/css">
	.topWrap {width: 100%;height: 2.50rem;border-bottom: 0.01rem solid #b9b9b9;position:fixed;top: 0;left: 0;background: #f8f8f8;z-index: 2}
	.topInner{background: #f8f8f8;}
	.topInner img{position: absolute;}
	.topInner .left{right: 2rem;top:1.30rem;width: 0.72rem;}
	.topInner .right{right:0.5rem;top:1.30rem;width: 0.72rem;}
	.topInner span{position: absolute;left: 45%;top:1.30rem;font-size: 0.5rem;}
	
	.topWrap .searchWrap{width: 100%;height: 2.50rem;background: #42c055;position: relative;left: 0;top: 0;}
	.searchWrap input{width: 10.90rem;height: 0.90rem;position: absolute;top: 1.40rem;left: 0.28rem;border:none;border-radius: 0.15rem;text-indent: 2em;border:none;}
	.searchWrap img{position: absolute;}
	.searchWrap .search{top: 1.60rem;left: 0.35rem;width: 0.51rem;z-index: 1}
	.searchWrap .big{top:  1.60rem;right: 1.52rem;width: 0.61rem;z-index: 1}
	.searchWrap .icon{top: 1.60rem;right: 0.3rem;width: 0.65rem;height:.51rem;z-index: 1}
</style>


