<template>
	<div>
		<template v-if = "isshow=='filmBrief'">
		<slot name="filmBrief"></slot>
		</template>
		<template v-if = "isshow=='filmread'">
		<slot name="filmBrief"></slot>
		</template>
		<template v-if = "isshow=='groupMusic'">
		<slot name="filmBrief"></slot>
		</template>
	</div>



	<!-- <div class="filmBrief clearfix">
		<slot name="touXiang"></slot>
		<div class="text">
			<h2>{{ title }}</h2>
			<div class="brief">{{ brief }}</div>
		</div>
		<span>{{ num }}<i>人</i></span>
		<div class="select"></div>
		
	</div> -->
</template>
<script type="text/javascript">
	export default{
		props:['title','brief','num','isshow']
	}
</script>
<style type="text/css">
	.clearfix:after{content: ".";display: block;visibility: hidden;height: 0;clear: both;}
	.filmBrief{width:11.46rem;margin:.5rem 0 .5rem .45rem;}
	.filmBrief .touXiang{width: 1.5rem;float: left;height: 1.5rem}
	.filmBrief .text{width: 6.95rem;height: 1.40rem;float: left;padding-left: .2rem}
	.filmBrief h2{font-size: .5rem;color: #494949;line-height: .6rem}
	.filmBrief .brief{width: 6.95rem;height: 1.1rem;font-size: .30rem;color: #9b9b9b;overflow: hidden;}
	.filmBrief span{font-size: .34rem;color: #9b9b9b;padding-left: .2rem;float: left; padding-right: .1rem;margin-top: .6rem}
	.filmBrief .select{width: .7rem;height: .7rem;border:.01rem solid #42bd56;border-radius: 50%;float: left;margin-top: .6rem;background:url(../../assets/images/ic_book_version_filter_check.png) no-repeat center; background-size: cover;}
	.filmBrief i{font-style: normal;}
</style>


