import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Home from '../pages/home/home'
import Film from '../pages/film/film'
import Broadcast from '../pages/broadcast/broadcast'
import Group from '../pages/group/group'
import Mine from '../pages/mine/mine'
import City from '../pages/film/city'
import Cinema from '../pages/film/cinema'
import Music from '../pages/film/music'
import Read from '../pages/film/read'
import Tv from '../pages/film/tv'
import Tetailed from '../pages/home/detailed'
import Destail from '../pages/home/destail'






Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      // children:[
      // {
      //   path:'destail',
      //   name:'Destail',
      //   component:Destail
      // }

      // ]
    },
    {
      path: '/film',
      name: 'Film',
      component: Film,
      children:[
      {path:'',component:Cinema},
      {path:'city',component:City},
      {path:'cinema',component:Cinema},
      {path:'music',component:Music},
      {path:'read',component:Read},
      {path:'tv',component:Tv}
      ]
    },
    {
      path: '/broadcast',
      name: 'Broadcast',
      component: Broadcast
    },
    {
      path: '/group',
      name: 'Group',
      component: Group
    },
    {
      path: '/mine',
      name: 'Mine',
      component: Mine
    },
    {
      path:'/detailed',
      name:'Tetailed',
      component:Tetailed


    },
    {
      path:'/destail',
      name:'Destail',
      component:Destail


    }

  ]
})



