<template>
<div>
	<template v-if = "isshow=='hotfilm'">
	<slot name="movieWrap"></slot>
	<div class="film"> 
			<span class="img"><slot name="filmImg"></slot></span>
			<p><span class="text">{{ txt }}</span></p>
			<div class="clearfix">
				<slot name="start"></slot>
				<span class="nums">{{ num }}</span>
			</div>
	</div>
	</template>
	<template v-if = "isshow=='soonfilm'">
	<div class="film"> 
			<span class="img"><slot name="filmImg"></slot></span>
			<p><span class="text">{{ txt }}</span></p>
			<div class="clearfix">
				<!-- <slot name="start"></slot> -->
				<span class="txts">{{ txts }}</span>
			</div>
	</div>
	</template>
	<template v-if = "isshow=='star'">
	<div class="film"> 
			<span class="img"><slot name="filmImg"></slot></span>
			<p><span class="text">{{ txt }}</span></p>
			<div class="clearfix">
				<!-- <slot name="start"></slot> -->
				<span class="txts">{{ txts }}</span>
			</div>
	</div>
	</template>
</div>
</template>
<script type="text/javascript">
	
	export default {
		props: ["txt","num","isshow","txts"],
	}


</script>
<style type="text/css">
	.film .clearfix:after{content: ".";display: block;visibility: hidden;height: 0;clear: both;}
	.film{width: 100%;font-size: 0; text-align: center;float: left;}
	.film .img{text-align: center;}
	.film p{text-align: left;margin-left: 0.5rem}
	.film .img img{width: 3rem;height: 4.18rem;}
	.film .text{font-size: 0.35rem;color: #494949;height: 0.7rem;line-height: 0.7rem;display: block;}
	.film .starts{height: 0.48rem;text-align: left;float: left;margin-left: 0.4rem;}
	.film .starts img{height: 0.48rem;float: left;}
	.film .nums{font-size: .14rem;color: #999;float: left;}
	.film .txts{font-size: 0.20rem;margin-left: 0.42rem;float: left;color: #999}

</style>


