
<template>
	<div class="footerWrap">
		<Item txt="首页" index="home" @change="getVal" :sel="selected">
			<img src="../assets/images/ic_tab_home_normal.png"  slot="grayImg"/>
			<img src="../assets/images/ic_tab_home_active.png" slot="greenImg"/>
		</Item>
		<Item txt="书影音"  index="film" @change="getVal" :sel="selected">
			<img src="../assets/images/ic_tab_subject_normal.png" slot="grayImg"/>
			<img src="../assets/images/ic_tab_subject_active.png" slot="greenImg"/>

		</Item>
		<Item txt="广播"  index="broadcast" @change="getVal" :sel="selected"> 
			<img src="../assets/images/ic_tab_status_normal.png" slot="grayImg"/>
			<img src="../assets/images/ic_tab_status_active.png" slot="greenImg"/>
		</Item>
		<Item txt="小组"  index="group" @change="getVal" :sel="selected">
			<img src="../assets/images/ic_tab_group_normal.png" slot="grayImg"/>
			<img src="../assets/images/ic_tab_group_active.png" slot="greenImg"/>
		</Item>
		<Item txt="我的"  index="mine" @change="getVal" :sel="selected">
			<img src="../assets/images/ic_tab_profile_normal.png" slot="grayImg"/>
			<img src="../assets/images/ic_tab_profile_active.png" slot="greenImg"/>
		</Item>
	</div>
</template>


<script type="text/javascript">
	import Item from './item'
	export default {
		components: {
			Item
		},
		data: function () {
			// body...
			return{
				selected: 'home'
			}
		},
		methods: {
			getVal: function (val) {
				// body...
				this.selected=val
			}
		}



	}
</script>
<style type="text/css">
	.footerWrap{width: 100%;height: 1.48rem;background: #f7f7f7;border-top: 0.01rem solid #999;position: fixed;left: 0;bottom: 0; z-index: 2}



</style>


