<template>
	<div class="filmWrap">
		<Top txt="书影音" >
			<img src="../../assets/images/ic_group_search.png" slot="topSearch" class="left"/>
			<img src="../../assets/images/ic_create_group_chat_blue.png" slot="topIcon" class="right"/>
		</Top>
		<ul>
			<li :class="{active:index==1}" @click="index=1">
				<router-link to="/film/cinema" >电影</router-link>
			</li>
			<li :class="{active:index==2}" @click="index=2">
				<router-link to="/film/read">读书</router-link>
			</li>
			<li :class="{active:index==3}" @click="index=3">
				<router-link to="/film/tv">电视</router-link>
			</li>
			<li :class="{active:index==4}" @click="index=4">
				<router-link to="/film/city">同城</router-link>
			</li>
			<li :class="{active:index==5}" @click="index=5">
				<router-link to="/film/music">音乐</router-link>
			</li>
		</ul>
		<router-view></router-view>
	
	</div>
</template>
<script type="text/javascript">
	import Top from '../../components/top'
	
	export default{
		components:{
			Top,
			
		},
		data: function (argument) {
			// body...
			return{
				index:1
			}
		}

	}

</script>
<style type="text/css">
	.filmWrap ul{width: 100%;height: 1.20rem;border-bottom: 0.01rem solid #e4e4e4;padding-bottom: 0.03rem;margin-top: 2.5rem;text-align: center;}
	.filmWrap li{float: left;width: 20%;list-style: none;height: 1.20rem;}
	.filmWrap li a{text-decoration: none;font-size: 0.44rem;color: #9a9a9a;display: block;line-height: 1.20rem;}
	.filmWrap .active{border-bottom: 0.03rem solid #63c882;}
	.filmWrap .active a{color:#63c882; }
</style>
 


