<template>
	<div > 
		<div class="destail" v-for = "item in destails">
			<h3>{{item.title}}</h3>
			<div ><span class="type">{{item.category_name}}</span><span class="adress">{{ item.address}}</span></div>
			<p v-html="item.content"></p>
		</div>
		
	</div>
</template>
<script type="text/javascript">
import request from 'superagent'
	import jsonp from 'superagent-jsonp'
	export default{
		data:function(){
			return{
				destails:[],
				
			}
		},

	 mounted: function(){
	 	var	$this = this
			request.get("https://api.douban.com/v2/event/"+this.$route.params.id)
		        .use(jsonp)
		        .end((err,res) => {
		        if (!err) {

		        	$this.destails.push(res.body)  
		        	// console.log($this.destails)
		     	 		}
		  			}) 
				}
	}
</script>
<style type="text/css">

.destail h3{font-size: .5rem; color: black;text-align: center;}
.destail span{font-size: .14rem;margin-right: .20rem;padding-left: .2rem}
.destail p{font-size: .12rem;text-indent: 1em;padding:0 .10rem;}

	
</style>


