<template>
	<div class="mineItem">
		<div class="mineInner">
			<slot name="mineImg"></slot>
			<span>{{ txt }}</span>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		props: ["txt"]
	}
</script>
<style type="text/css">
	.mineItem{width: 25%;height: 3.10rem;float: left;}
	.mineItem img{width: .90rem;height: .90rem}
	.mineInner{margin-top: 0.6rem}
	.mineItem span{font-size: 0.22rem;display: block;height: 0.8rem;}
</style>


