<template>
	<div>
		<div class="detailed" v-for = "(item,index) in detailedArr" v-show = "index == detailedArr[index].id">

			<h1>{{ item.title }}</h1>
			<div class="wrap"><span class="name">{{ item.name }}</span><span class="time">{{ item.time }}</span></div>
			<img :src="item.img1">
			<p>{{ item.content1 }}</p>
			<img :src="item.img2">
			<p>{{ item.content2 }}</p>
		</div>
	</div>


</template>
<script type="text/javascript">

	import request from 'superagent'
	import jsonp from 'superagent-jsonp'
	export default{
		data:function(){
			return{
				detailedArr:[]
			}
		},

		mounted: function(){
			var _this = this
			this.axios.get('static/data/content.json').then(function(res){
				 _this.detailedArr = res.data
				    // console.log(res)

			})
		}



	}



</script>
<style type="text/css">
	.detailed h1{font-size: .5rem;height: .20rem;display: block;}
	.detailed .wrap{padding: .10rem}
	.detailed .name{font-size: .20rem;}
	.detailed .time{font-size: .16rem;color: #666}
	.detailed p{font-size: .16rem}
	.detailed img{width: 100%;height: 3.00rem;}

</style>


