<template>
	<div class="itemWrap" @click="fn">
		<!-- <img src="../assets/img/1.png" height="52" width="62"> -->
		<span v-show="bol"><slot name="grayImg"></slot></span>
		<span v-show="!bol"><slot name="greenImg"></slot></span>

		<span :class="{color:!bol}" class="wz">{{ txt }}</span>
	</div>

</template>
<script type="text/javascript">
	export default {
	
		props: ["txt","index","sel"],
		methods: {
			fn: function () {
				this.$emit("change",this.index)
				this.$router.push('/'+this.index)
			}
		},
		computed: {
			bol: function () {
				// body...
				if (this.index==this.sel) {return false}
					return true
			}
		}



	}

</script>
<style type="text/css">
	.itemWrap{width: 20%;float: left;text-align: center;line-height: 0.4rem;}
	.itemWrap span{font-size: .3rem;color: #bfbfbf;}
	.itemWrap .color{color: green}
	.itemWrap img{width:1rem;height:1rem;}
	.itemWrap .wz{display: block;}


</style>



